.sidebar {
    width: 16%;
    height: 100vh;
    background-color: #191d2b;
    position: fixed;
    border-right: 1px solid #2e344e;
}

.main-content {
    width: 84%;
    margin-left: 16%;
    background-color: #10121B;
    min-height: 100vh;
    display: grid;
    position: relative;
}

.main-content .content {
    margin: 5rem 15rem;
}