.ServicesSection {}

.service {
    width: 85%;
    background-color: #191D2B;
    border-left: 1px solid #2e344e;
    border-right: 1px solid #2e344e;
    border-bottom: 1px solid #2e344e;
    border-top: 8px solid #2e344e;
    transition: all .4s ease-in-out;
}

.service:hover {
    border-top: 8px solid #037FFF;
}

.service-content {
    padding: 1rem;
}

.s-title {
    font-size: 1.6rem;
    font-weight: 500;
    position: relative;
    padding-bottom: 1rem;
    margin: 1rem 0;
}

.s-title::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 30%;
    height: 1px;
    background-color: #2e344e;
}

.services-container {
    display: flex;
    justify-content: space-between;
    margin-top: 4rem;
}

h5 {
    color: #c2c5d1;
}